@import "styles/variables";
@import "styles/mixins";

.item {
  padding-bottom: 1.5rem;
  overflow: hidden;

  &:not(:first-child) {
    .description,
    .icon > * {
      transition-delay: 0.25s;
    }
  }
}

.description {
  @include regular($colorDarkest);

  text-align: right;
  transform: translateY(-100%);
  transition-duration: $transitionDuration;
}

.descriptionVisible {
  transform: translateY(0);
}

.descriptionAfter {
  text-align: left;
}
