@import "../../../styles/variables";
@import "../../../styles/mixins";

.item {
  padding-bottom: 1.5rem;
}

.itemTypes {
  margin-top: 3rem;
}

.icon {
  display: block;
  margin: 0 auto;
  width: 16rem;
  fill: $colorMediumDark;
}

.iconAfter {
  grid-column: 3 / 4;
  fill: $colorHighlight;
}

.type {
  @include nav($colorLightest, 700, true);

  display: block;
  padding: 1.6rem 3rem;
  background-color: $colorMediumDark;
  border-radius: 3rem;
  text-align: center;
}

.typeAfter {
  grid-column: 3 / 4;
  background-color: $colorHighlight;
}
