@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: flex;
  flex-direction: column;
  padding: 4rem 2rem;
  min-height: 40rem;
  background-color: $colorLightest;
  border-radius: 5rem;
  box-shadow:
    0 1rem 8rem rgba(202, 60, 47, 0.07),
    0 4rem 3.3rem rgba(202, 60, 47, 0.05),
    0 2.2rem 1.8rem rgba(202, 60, 47, 0.04),
    0 1.2rem 1rem rgba(202, 60, 47, 0.035);
}

.heading {
  text-align: center;
}

.list {
  margin: 2rem 0 0 0;
}

.price {
  margin-top: auto;
  text-align: center;
}

.label {
  @include nav($colorDarkest, 700);
}

.value {
  @include descriptionPromo($colorHighlight, 700);

  margin-left: 1rem;
}
