@import "styles/variables";
@import "styles/mixins";

.section {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 23.5rem;
    left: calc(50% - 5rem);
    width: 100vw;
    height: 48.5rem;
    background-color: $colorMediumDark;
    border-top-left-radius: 1.5rem;
    border-bottom-left-radius: 30rem;
    opacity: 0.4;
  }
}

.circles {
  width: 68.2rem;
}

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 65rem 1fr;
  padding-bottom: 19rem;
}

.wrapperShadow {
  &::before {
    content: "";
    position: absolute;
    bottom: 14rem;
    right: -1.5rem;
    width: 65.44rem;
    height: 9.76rem;
    background-image: url("../../../assets/images/Shadow.png");
    background-size: cover;
    z-index: 2;
  }
}

.frame {
  position: absolute;
  top: 8.5rem;
  right: -10rem;
  clip-path: polygon(
      10.2rem 12.95rem,
      50.8rem -0.1rem,
      50.9rem 0,
      51rem 0.2rem,
      51.5rem 0.4rem,
      51.8rem 0.5rem,
      62rem 28rem,
      62.2rem 28.5rem,
      62.22rem 29.4rem,
      63.2rem 29.5rem,
      82rem 39.2rem,
      82.4rem 39.7rem,
      82.4rem 40.2rem,
      82.3rem 40.3rem,
      82.2rem 40.4rem,
      60rem 49.9rem,
      39rem 59rem,
      38.5rem 59.1rem,
      37.4rem 59rem,
      23.5rem 47rem,
      22.7rem 46.3rem,
      22.5rem 45.4rem,
      22.5rem 45.4rem,
      22.2rem 45.2rem,
      22rem 45rem,
      21.9rem 44.9rem,
      21.8rem 44.8rem,
      21.7rem 44.7rem,
      21.6rem 44.6rem,
      9.8rem 15rem,
      9.7rem 13.9rem,
      9.8rem 13.8rem,
      10rem 13.2rem,
      10.3rem 12.8rem,
  );
}

.video {
  width: 82.6rem;
}

.labelNext {
  @include nav($colorDarkest, 500, true);

  position: absolute;
  top: calc(64.2rem + 7rem);
  left: 7rem;
  display: flex;
  flex-direction: column;
  //padding-left: 12.6rem;
  //padding-left: 12.6rem;
  letter-spacing: 4px;
  transform: rotate(-90deg);
  transform-origin: 0 50%;

  span {
    padding-left: 12.6rem;
    text-transform: lowercase;
  }
}

.arrow {
  //position: absolute;
  //top: -29rem;
  //left: 31rem;
  width: 64.2rem;
  fill: $colorHighlight;
  //transform: rotate(-270deg);
}

.textBlock {
  position: relative;
  padding-top: 32rem;
}

.description {
  @include descriptionPromo;

  margin: 3.5rem 0 0 0;
  padding-right: 40%;
}

.button {
  margin-top: 5.5rem;
}
