@import "styles/variables";

@keyframes lift {
  0% {
    transform: translateY(15rem);
  }

  100% {
    transform: translateY(0);
  }
}

.wrapper {
  position: relative;
  --section-radius: 7rem;
  background-color: $colorHighlight;
  border-top-right-radius: var(--section-radius);
  border-top-left-radius: var(--section-radius);
  margin-bottom: -7rem;
  padding-bottom: 7rem;
  transform: translateY(15rem);

  &::before {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 50vh;
    background-color: $colorHighlight;
  }
}

.wrapperVisible {
  animation-name: lift;
  animation-duration: 0.6s;
  animation-timing-function: ease-out;
  animation-fill-mode: both;
}

.wrapperLight {
  background-color: $colorLightest;

  &::before {
    background-color: $colorLightest;
  }
}

.wrapperMedium {
  background-color: $colorMedium;

  &::before {
    background-color: $colorMedium;
  }
}

.wrapperShort {
  &::before {
    display: none;
  }
}

.wrapperOverflow {
  overflow: hidden;
}
