@import "styles/variables";

.wrapper {
  position: relative;
  padding-top: 3.7rem;
  padding-left: 3.5rem;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 7rem;
    height: 7rem;
    background-color: $colorHighlight;
    border-radius: 50%;
    transform-origin: center;
    transform: scale(0);
    transition-duration: 0.5s;
  }

  & > * {
    position: relative;
  }
}

.icon {
  position: absolute;
  top: 4rem;
  right: -6.2rem;
  fill: $colorHighlight;
  transition-duration: 0.5s;
  opacity: 0;
  transform: rotate(-60deg);
  transform-origin: -3rem center;
  transition-delay: 1s;
}

.heading,
.description {
  transition-duration: 0.5s;
  transform: translateY(-100%);
  transition-delay: 0.5s;
  opacity: 0;
}

.description {
  color: $colorDarkest;

  margin-top: 2rem;
}

.wrapperVisible {
  .heading,
  .description {
    transform: translateY(0);
  }

  .heading {
    opacity: 1;
  }

  .description {
    opacity: 0.6;
  }

  &::before {
    transform: scale(1);
  }

  .icon {
    transform: rotate(0);
    opacity: 1;
  }
}
